.list {
  flex-wrap: wrap;
  justify-content: space-around;
  list-style: none;
  display: flex;
}

.container {
  margin: 100px 50px 50px;
  display: flex;
}

.content {
  width: 156px;
  border: 1px solid #000;
  margin: 0 auto;
}

.item {
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  border: 1px solid #000;
  font-size: 25px;
  line-height: 50px;
  transition: background .1s;
  display: inline-block;
}

.item:hover {
  background-color: rgba(241, 241, 241, .047);
}

